@import url(https://fonts.googleapis.com/css2?family=Love+Ya+Like+A+Sister&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
/* base styles */
* {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Love Ya Like A Sister', cursive;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background-image: url(/memoryabc/static/media/bgr.27ecd0e8.png);
  background-size: contain;
  background-repeat: repeat-y;
  background-color: #e4d7f5;
  color: rgb(39, 16, 49);
}
h1 {
  margin: 30px 0 40px 0;
  font-weight: normal;
  font-family: 'Luckiest Guy', cursive;
  font-size: 2em;
  line-height: 1em;
  font-weight: 700;
  color: #f5f5f5;
  text-shadow: 1px 1px 1px #919191, 1px 2px 1px #919191, 1px 3px 1px #919191,
    1px 4px 1px #919191, 1px 5px 1px #919191, 1px 6px 1px #919191,
    1px 7px 1px #919191, 1px 8px 1px #919191, 1px 9px 1px #919191,
    1px 10px 1px #919191, 1px 18px 6px rgba(16, 16, 16, 0.4),
    1px 22px 10px rgba(16, 16, 16, 0.2), 1px 25px 35px rgba(16, 16, 16, 0.2),
    1px 30px 60px rgba(16, 16, 16, 0.4);
}
button {
  font-family: 'Love Ya Like A Sister', cursive;
}

.card {
  position: relative;
  cursor: pointer;
}

.card img {
  width: 100%;
  display: block;
  border: 6px solid #fff;
  border-radius: 6px;
  box-sizing: border-box;
  box-shadow: 0px 0px 8px 1px rgba(0, 0, 0, 0.3);
}

.card .front {
  transform: rotateY(90deg);
  position: absolute;
  height: 100%;
  background: #fff;
  padding: 18%;
  box-sizing: border-box;
  transition: all ease-in 0.2s;
}
.flipped .front {
  box-shadow: 0px 0px 12px 1px rgba(0, 0, 0, 0.6);
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}

/*--- animations ---*/
.card-0,
.card-2,
.card-5,
.card-7,
.card-8,
.card-10 {
  animation: slide-in-elliptic-left-fwd 1.5s
    cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.card-1,
.card-3,
.card-4,
.card-6,
.card-9,
.card-11 {
  animation: slide-in-elliptic-right-fwd 1.4s
    cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.2s both;
}
@keyframes slide-in-elliptic-left-fwd {
  0% {
    transform: translateX(-800px) rotateY(30deg) scale(0);
    transform-origin: -100% 50%;
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotateY(0) scale(1);
    transform-origin: 1800px 50%;
    opacity: 1;
  }
}

@keyframes slide-in-elliptic-right-fwd {
  0% {
    transform: translateX(800px) rotateY(-30deg) scale(0);
    transform-origin: -100% 50%;
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotateY(0) scale(1);
    transform-origin: -1800px 50%;
    opacity: 1;
  }
}

.App {
  max-width: 860px;
  margin: 0 auto 40px auto;
}
.App .new-btn {
  background: hsl(340deg 100% 32%);
  border-radius: 12px;
  border: none;
  padding: 0;
  outline-offset: 4px;
  cursor: pointer;
  font-size: 1em;
}
.App .btn-front {
  display: block;
  padding: 12px 42px;
  border-radius: 12px;
  font-size: 1.25rem;
  background: hsl(345deg 100% 47%);
  color: white;
  transform: translateY(-6px);
}

.App .new-btn:active .btn-front {
  transform: translateY(-2px);
}

.App .card-grid {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}
@media (max-width: 768px) {
  .App .card-grid {
    padding: 0 10px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 15px;
  }
}

.App .turns-box {
  margin-top: 30px;
}
.App .turns {
  color: red;
}
.App .credit {
  font-family: Verdana, sans-serif;
  margin-top: 40px;
  text-decoration: none;
  color: silver;
  font-size: 0.4em;
}
.App .credit a {
  text-decoration: none;
  color: #93c7d7;
}

